<template>
  <div
    class="mb-2"
    :class="{
      'mt-4 mt-4': $vuetify.breakpoint.smAndDown,
      'mt-n6': $vuetify.breakpoint.mdAndUp,
    }"
    md="3"
  >
    <v-tabs
      v-model="active_tab"
      color="var(--button-tertiary)"
      grow
      id="exchange-tab"
      slider-color="var(--button-tertiary)"
      height="56px"
      slider-size="1"
    >
      <v-tab
        v-for="tab of tabs"
        :key="tab.index"
        :ripple="false"
      >
        <v-col class="d-flex justify-center align-center">
          <p
            class="ma-0 mt-0 pb-3 headingSubTitleBoldPrimary"
            v-html="tab.name"
          />
          <div
            v-if="tab.index !== active_tab"
            class="v-tabs-slider-wrapper"
            style="height: 1px; width: 100%; color: #D9D9D9"
          >
            <div class="v-tabs-slider" />
          </div>
        </v-col>
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="active_tab">
      <v-tab-item v-for="tab of tabs" :key="tab.index">
        <slot v-if="tab.index === 0" />
        <refund-card
          v-if="tab.index === 1"
          @click.once="returnReasonClicked"
          :is-map-loading="isMapLoading"
        />
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import { PAGE_TITLES } from 'branding/strings.json'

export default {
  components: {
    RefundCard: () => import('@/components/organism/RefundView.vue')
  },
  name: 'ExchangeTabWrapper',
  data: () => ({
    active_tab: 0,
    tabs: [
      {
        index: 0,
        name:
          "<div class='headingSubTitleBoldPrimary mt-2'>Exchange</div><div class='headingSubTitleSmallBold mt-1'>Ships immediately</div>"
      },
      {
        index: 1,
        name:
          "<div class='headingSubTitleBoldPrimary mt-2'>Refund</div><div class='headingSubTitleSmallBold mt-1'>After item received</div>"
      }
    ]
  }),
  watch: {
    active_tab: {
      handler () {
        this.active_tab === 0 ? document.title = PAGE_TITLES.EXCHANGE : document.title = PAGE_TITLES.REFUND
      }
    }
  },
  props: {
    returnReasonClicked: {
      type: Function,
      required: true
    },
    isMapLoading: {
      required: true,
      type: Boolean
    }
  },
  methods: {},
  computed: {
  },
  created () {
  },
  mounted () { // lands on exchanges tab
    document.title = PAGE_TITLES.EXCHANGE
  },
  beforeUnmount () {}
}
</script>

<style>
#exchange-tab .v-slide-group__prev {
  display: none !important;
}
</style>
